import React, { Component } from 'react'
import {Link,} from 'react-router-dom';

export default class Share extends Component {
    constructor(){
        super();
        this.state = {
            data: []
        }
    }
    componentDidMount(){
        let page = this.props.match.params.page;
        fetch('https://cnodejs.org/api/v1/topics?tab=share&&page='+page)
        .then((res)=>res.json())
        .then((res)=>{
            this.setState({data:res.data});
        })
    }
    componentDidUpdate(prevProps,prevState){
        if(prevProps.match.params.page!==this.props.match.params.page){
            let page = this.props.match.params.page;
            fetch('https://cnodejs.org/api/v1/topics?tab=share&&page='+page)
                .then((res)=>res.json())
                .then((res)=>{
                        this.setState({data:res.data});
                    })
        }
    }
    render() {
        return (
            <div>
                
                {
                    this.state.data.map((item)=>(
                        <ul>
                            <li style={{height:'50px'}}>
                                
                                <p style={{}}>
                                
                                <img src={item.author.avatar_url} style={{width:'30px',height:'30px',float:'left',marginTop:'-5px;'}}/>
                                {item.reply_count}/{item.visit_count}
                               
                                </p>
                                <Link to={'/topics/'+item.id}  style={{marginLeft:'80px',fontSize:'14px', float:'left'}}>{item.title}</Link>
                                
                                <p style={{float:'right',marginTop:'-15px'}}><img src={item.author.avatar_url} style={{width:'30px',height:'30px',}} />
                                2分钟前
                                </p>
                            </li>
                            <hr/>
                        </ul>    
                    ))
                }
                <div className='bt' style={{}}>
                            <button>1</button>
                            <button>2</button>
                            <button>3</button>
                            <button>4</button>
                            <button>5</button>
                            <button>6</button>
                            <button>7</button>
                            <button>8</button>
                            <button>9</button>
                            <button>10</button>
                        </div>
            </div>
        )
    }
}